div.gallery {
  border: 1px solid #ccc;
}

div.gallery:hover {
  border: 1px solid #777;
}

div.gallery img {
  width: 100%;
  height: auto;
}

div.desc {
  text-align: center;
  padding: 15px;
}

* {
  box-sizing: border-box;
}

.responsive {
  float: left;
  width: 25%;
